 .blobs {
   filter: url('#goo');
   position: absolute;
   top: 50px;
   left: 0;
   bottom: 0;
   right: 0;
 }

 @keyframes blob-left-top-anim {
   0% {
     transform: scale(1.1) translate(0, 0);
   }
   33% {
     transform: scale(0.9) translate(-120px, 0);
   }
   62% {
     transform: scale(0.7) translate(-120px, -120px);
   }
   94% {
     transform: scale(1.1) translate(0, 0);
   }
 }

 @keyframes blob-right-top-anim {
   0% {
     transform: scale(1.1) translate(0, 0);
   }
   33% {
     transform: scale(0.9) translate(120px, 0);
   }
   64% {
     transform: scale(0.7) translate(120px, -120px);
   }
   96% {
     transform: scale(1.1) translate(0, 0);
   }
 }

 @keyframes blob-left-bottom-anim {
   0% {
     transform: scale(1.1) translate(0, 0);
   }
   33% {
     transform: scale(0.9) translate(-120px, 0);
   }
   66% {
     transform: scale(0.7) translate(-120px, 120px);
   }
   98% {
     transform: scale(1.1) translate(0, 0);
   }
 }

 @keyframes blob-right-bottom-anim {
   0% {
     transform: scale(1.1) translate(0, 0);
   }
   33% {
     transform: scale(0.9) translate(120px, 0);
   }
   68% {
     transform: scale(0.7) translate(120px, 120px);
   }
   100% {
     transform: scale(1.1) translate(0, 0);
   }
 }

 .blob {
   position: absolute;
   background: orange;
   left: 50%;
   top: 50%;
   width: 170px;
   height: 100px;
   line-height: 100px;
   text-align: center;
   color: black;
   font-size: 30px;
   border-radius: 100%;
   margin-top: -50px;
   margin-left: -50px;
   animation: blob-left-top-anim cubic-bezier(0.77, 0, 0.175, 1) 4s infinite;
 }

 .blob:nth-child(2) {
   animation-name: blob-right-top-anim;
 }

 .blob:nth-child(3) {
   animation-name: blob-left-bottom-anim;
 }

 .blob:nth-child(4) {
   animation-name: blob-right-bottom-anim;
 }

